<template>
  <div class="asp">
    <div class="asp-content">
      <div class="asp-form">
        <div class="asp-table">
          <el-dialog
            title
            :visible.sync="centerDialogVisiblesStatus"
            :show-close="false"
            width="35%"
            center
          >
            <div style="text-align:center">
              <div class="loading">
                <i style="font-size:60px;color: #fabb14;" class="el-icon-warning"></i>
              </div>
              <div class="loading-texts">
                
                
                
              </div>
              <div style="text-align:center">
                <el-button
                  style="width:100px;height:40px;line-height:0px;margin:15px 0 0 0"
                  plain
                  @click="centerDialogVisiblesStatus = false"
                ></el-button>
                <el-button
                  style="width:100px;height:40px;line-height:0px;margin:15px 0 0 20px"
                  type="primary"
                  @click="centerDialogVisiblesStatus = false"
                ></el-button>
              </div>
            </div>
          </el-dialog>
          <el-dialog
            title=""
            :visible.sync="centerDialogVisible"
            :show-close="false"
            width="35%"
            style="margin:200px 0 0 0"
            center
          >
            <div>
              <div class="loading-text">
                <el-form
                  :model="ruleForms"
                  :rules="rule"
                  ref="ruleForm"
                  label-width="100px"
                  class="demo-ruleForm"
                >
                  <el-form-item label="" prop="city">
                    <el-cascader v-model="ruleForms.city" :props="props" :options="cityList"></el-cascader>
                  </el-form-item>
                  <el-form-item label="" prop="detailAddress">
                    <el-input type="textarea" v-model="ruleForms.detailAddress"></el-input>
                  </el-form-item>
                </el-form>
              </div>
              <div style="text-align:center">
                <el-button
                  style="width:100px;height:40px;line-height:0px;margin:35px 0 0 0"
                  plain
                  @click="centerDialogVisible = false"
                ></el-button>
                <el-button
                  style="width:100px;height:40px;line-height:0px;margin:35px 0 0 20px"
                  type="primary"
                  @click="addAddress"
                ></el-button>
              </div>
            </div>
          </el-dialog>
          <el-dialog
            title=""
            :visible.sync="centerDialogVisibles"
            :show-close="false"
            width="35%"
            style="margin:200px 0 0 0"
            center
          >
            <div>
              <div class="loading-text">
                <el-form
                  :model="ruleFormes"
                  :rules="rule"
                  ref="ruleForm"
                  label-width="100px"
                  class="demo-ruleForm"
                >
                  <el-form-item label="" prop="city">
                    <el-cascader v-model="ruleFormes.city" :props="props" :options="cityList"></el-cascader>
                  </el-form-item>
                  <el-form-item label="" prop="detailAddress">
                    <el-input type="textarea" v-model="ruleFormes.detailAddress"></el-input>
                  </el-form-item>
                </el-form>
              </div>
              <div style="text-align:center">
                <el-button
                  style="width:100px;height:40px;line-height:0px;margin:35px 0 0 0"
                  plain
                  @click="centerDialogVisible = false"
                ></el-button>
                <el-button
                  @click="editAddress"
                  style="width:100px;height:40px;line-height:0px;margin:35px 0 0 20px"
                  type="primary"
                ></el-button>
              </div>
            </div>
          </el-dialog>
          <div style="margin:19px 0 0 0">
            <el-form
              :model="ruleForm"
              :rules="rules"
              ref="ruleForm"
              label-width="100px"
              class="demo-ruleForm"
            >
              <el-form-item label="" prop="positionName">
                <el-input style="width:240px" placeholder="" v-model="ruleForm.positionName"></el-input>
              </el-form-item>
              <el-form-item label="" prop="nature">
                <el-select style="width:240px" v-model="ruleForm.nature" placeholder="">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="2"></el-option>
                  <el-option label="" value="3"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="positionCatalog">
                <el-cascader
                  style="width:240px"
                  :options="positionCatalogList"
                  :props="propsTwo"
                  :show-all-levels="false"
                  v-model="ruleForm.positionCatalog"
                ></el-cascader>
              </el-form-item>
              <el-form-item label="" prop="salary">
                <el-select style="width:240px" v-model="ruleForm.salary" placeholder="">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="degree">
                <el-select style="width:240px" v-model="ruleForm.degree" placeholder="">
                  <el-option
                    v-for="item in optionsDegree"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="workYear">
                <el-select style="width:240px" v-model="ruleForm.workYear" placeholder="">
                  <el-option
                    v-for="item in optionsWorkYear"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-select style="width:500px" v-model="ruleForm.workCity" placeholder>
                  <el-option
                    v-for="item in addressList"
                    :key="item.id"
                    class="option"
                    :label="item.detail"
                    :value="item.id"
                  >
                    <span style="float: left">{{ item.detail }}</span>
                    <span
                      @click="deleteAddress(item)"
                      class="span-hover"
                      style="float: right; color: #8492a6; font-size: 13px"
                    ></span>
                    <span
                      @click="editAddressre(item)"
                      class="span-hover"
                      style="float: right; color: #8492a6; font-size: 13px;margin:0 10px 0 0"
                    ></span>
                  </el-option>
                  <el-option style="text-align:center">
                    <span @click="centerDialogVisible = true">
                      <i class="el-icon-plus"></i>
                    </span>
                  </el-option>
                </el-select>
                <br />
                <!-- <el-input
                  style="width:500px;margin:20px 0 0 0"
                  placeholder=""
                  type="textarea"
                  v-model="ruleForm.desc"
                ></el-input>-->
              </el-form-item>
              <el-form-item label="" prop="positionCatalogDetail">
                <el-input
                  style="width:500px;"
                  type="textarea"
                  placeholder="300"
                  v-model="ruleForm.positionCatalogDetail"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="JobSearch">
                <el-input
                  style="width:500px;"
                  type="textarea"
                  placeholder="300"
                  v-model="ruleForm.JobSearch"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="skills">
                <el-input
                  style="width:500px;"
                  type="textarea"
                  placeholder=""
                  v-model="ruleForm.skills"
                ></el-input>
              </el-form-item>
              <el-form-item label="HR" prop="HR">
                <el-select style="width:240px" v-model="ruleForm.HR" placeholder="HR">
                  <el-option
                    v-for="item in HRlist"
                    :key="item.id"
                    :label="item.realName"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input style="width:240px" v-model="ruleForm.email"></el-input>
                <el-button style="margin:0 0 0 20px" @click.prevent="addDomain"></el-button>
              </el-form-item>
              <el-form-item
                v-for="(domain, index) in ruleForm.domains"
                :label="''"
                :key="domain.key"
                :prop="'domains.' + index + '.value'"
                :rules="{
      required: true, message: '', trigger: 'blur'
    }"
              >
                <el-input style="width:240px" v-model="ruleForm.value"></el-input>
                <el-button style="margin:0 0 0 20px" @click.prevent="removeDomain(domain)"></el-button>
              </el-form-item>
              <el-form-item label="" prop="onlineTime">
                <el-date-picker style="width:240px" v-model="ruleForm.onlineTime" type="date" placeholder=""></el-date-picker>
              </el-form-item>
              <el-form-item label="" prop="offlineTime">
                <el-date-picker style="width:240px" v-model="ruleForm.offlineTime" type="date" placeholder=""></el-date-picker>
              </el-form-item>
              <!-- <el-form-item label="">
                <span style="width:240px">13</span>
              </el-form-item>-->
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import positionCatalog from "../../assets/positionCatalog.json";
import city from "../../assets/city.json";
import { CodeToTag } from "../../cookie.js";
export default {
  data() {
    return {
      ruleForm: {
        positionName: "",
        nature: "",
        positionCatalog: "",
        salary: "",
        degree: "",
        workYear: "",
        workCity: "",
        desc: "",
        positionCatalogDetail: "",
        JobSearch: "",
        skills: "",
        HR: "",
        email: "",
        domains: [],
        value: "",
        onlineTime: "",
        offlineTime: "",
        onlineDay: ""
      },
      props: {
        value: "tag",
        label: "tag",
        children: "children"
      },

      ruleForms: {
        detailAddress: "",
        city: ""
      },
      ruleFormes: {
        detailAddress: "",
        city: ""
      },
      centerDialogVisible: false,
      centerDialogVisibles: false,
      centerDialogVisiblesStatus:false,
      propsTwo: {
        value: "code",
        label: "tag",
        children: "children",
        emitPath: false
      },
      propsOne: {
        value: "code",
        label: "tag",
        children: "children",
        emitPath: false
      },
      options: [
        {
          value: "0",
          label: "1K"
        },
        {
          value: "1",
          label: "1K-2K"
        },
        {
          value: "2",
          label: "2K-4K"
        },
        {
          value: "3",
          label: "4K-6K"
        },
        {
          value: "4",
          label: "6K-8K"
        },
        {
          value: "5",
          label: "6K-8K"
        },
        {
          value: "6",
          label: "8K-10K"
        },
        {
          value: "7",
          label: "10K-15K"
        },
        {
          value: "8",
          label: "15K-25K"
        },
        {
          value: "9",
          label: "25K-35K"
        },
        {
          value: "10",
          label: "35K"
        }
      ],
      optionsDegree: [
        {
          value: "0",
          label: ""
        },
        {
          value: "1",
          label: "/"
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
          label: ""
        },
        {
          value: "5",
          label: ""
        },
        {
          value: "6",
          label: ""
        }
      ],
      optionsWorkYear: [
        {
          value: "0",
          label: ""
        },
        {
          value: "1",
          label: "1-3"
        },
        {
          value: "2",
          label: "3-5"
        },
        {
          value: "3",
          label: "5-10"
        },
        {
          value: "4",
          label: "10"
        }
      ],
      companyId: "",
      positionCatalogList: [],
      cityList: [],
      HRlist: [],
      addressList: [],
      rules: {
        positionName: [
          { required: true, message: "", trigger: "blur" }
          //   { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        nature: [
          { required: true, message: "", trigger: "change" }
        ],
        positionCatalog: [
          { required: true, message: "", trigger: "change" }
        ],
        salary: [
          { required: true, message: "", trigger: "change" }
        ],
        degree: [
          { required: true, message: "", trigger: "change" }
        ],
        workYear: [
          { required: true, message: "", trigger: "change" }
        ],
        workCity: [
          { required: true, message: "", trigger: "change" }
        ],
        positionCatalogDetail: [
          { required: true, message: "", trigger: "change" },
          {
            min: 0,
            max: 300,
            message: " 0  300 ",
            trigger: "blur"
          }
        ],
        JobSearch: [
          { required: true, message: "", trigger: "change" },
          {
            min: 0,
            max: 300,
            message: " 0  300 ",
            trigger: "blur"
          }
        ],
        HR: [{ required: true, message: "HR", trigger: "change" }],
        email: [
          { required: true, message: "", trigger: "change" }
        ],
        onlineTime: [
          { required: true, message: "", trigger: "change" }
        ],
        offlineTime: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      rule: {
        city: [{ required: true, message: "", trigger: "blur" }],
        detailAddress: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      id: "",
      status: ""
    };
  },
  methods: {
    //
    editAddressre(tab) {
      this.id = tab.id;
      this.centerDialogVisibles = true;
      this.ruleFormes.city = [tab.province, tab.city, tab.district];
      this.ruleFormes.detailAddress = tab.detail;
    },
    //
    deleteAddress(tab) {
      this.$http
        .delete(`/business-core/companies/addresses/${tab.id}`)
        .then(res => {
          if (res.data.code == "204") {
            this.ruleForm.workCity = "";
            this.address();
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //
    addAddress() {
      let params = {
        city: this.ruleForms.city[1],
        detail: this.ruleForms.detailAddress,
        district: this.ruleForms.city[2],
        province: this.ruleForms.city[0]
      };
      this.$http
        .post(`/business-core/companies/addresses`, params)
        .then(res => {
          if (res.data.code == "201") {
            this.centerDialogVisible = false;
            this.address();
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //
    editAddress() {
      let params = {
        city: this.ruleFormes.city[1],
        detail: this.ruleFormes.detailAddress,
        district: this.ruleFormes.city[2],
        province: this.ruleFormes.city[0]
      };
      this.$http
        .put(`/business-core/companies/addresses/${this.id}`, params)
        .then(res => {
          if (res.data.code == "200") {
            this.centerDialogVisibles = false;
            this.address();
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //
    address() {
      this.$http
        .get(`/business-core/companies/addresses`)
        .then(res => {
          if (res.data.code == "200") {
            this.addressList = res.data.data;
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //
    brief() {
      this.$http
        .get("/business-core/companyes/brief")
        .then(res => {
          if (res.data.code == "200") {
            this.companyId = res.data.data.id;
            this.address();
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //HR
    ListHR() {
      this.$http
        .get("/business-core/companyAccounts/listHr")
        .then(res => {
          if (res.data.code == "200") {
            this.HRlist = res.data.data;
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //
    state() {
      this.$http
        .get("/business-core/companyes/state")
        .then(res => {
          if (res.data.code == "200") {
            this.status = res.data.data;
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //
    submitForm(formName) {
      if (this.status === "ONLINE") {
        this.$refs[formName].validate(valid => {
          let degreeName;
          switch (this.ruleForm.degree) {
            case "0":
              degreeName = "";
              break;
            case "1":
              degreeName = "/";
              break;
            case "2":
              degreeName = "";
              break;
            case "3":
              degreeName = "";
              break;
            case "4":
              degreeName = "";
              break;
            case "5":
              degreeName = "";
              break;
            case "6":
              degreeName = "";
              break;
          }
          let natureName;
          switch (this.ruleForm.nature) {
            case "0":
              natureName = "";
              break;
            case "1":
              natureName = "";
              break;
            case "2":
              natureName = "";
              break;
          }
          let salaryMin;
          let salaryMax;
          switch (this.ruleForm.salary) {
            case "0":
              salaryMin = 0;
              salaryMax = 1;
              break;
            case "1":
              salaryMin = 1;
              salaryMax = 2;
              break;
            case "2":
              salaryMin = 2;
              salaryMax = 4;
              break;
            case "3":
              salaryMin = 4;
              salaryMax = 6;
              break;
            case "4":
              salaryMin = 6;
              salaryMax = 8;
              break;
            case "5":
              salaryMin = 8;
              salaryMax = 10;
              break;
            case "6":
              salaryMin = 10;
              salaryMax = 15;
              break;
            case "7":
              salaryMin = 15;
              salaryMax = 25;
              break;
            case "8":
              salaryMin = 25;
              salaryMax = 35;
              break;
            case "9":
              salaryMin = 35;
              salaryMax = null;
              break;
          }
          let workAgeMin;
          let workAgeMax;
          switch (this.ruleForm.workYear) {
            case "0":
              workAgeMin = 0;
              workAgeMax = 0;
              break;
            case "1":
              workAgeMin = 1;
              workAgeMax = 3;
              break;
            case "2":
              workAgeMin = 3;
              workAgeMax = 5;
              break;
            case "3":
              workAgeMin = 5;
              workAgeMax = 10;
              break;
            case "4":
              workAgeMin = 10;
              workAgeMax = null;
              break;
            default:
              workAgeMin = null;
              workAgeMax = null;
              break;
          }
          let logoutTime = this.ruleForm.onlineTime.getTime();
          let publishedTime = this.ruleForm.offlineTime.getTime();
          console.log(this.ruleForm.workCity)
          if (valid) {
            let params = {
              addressId: this.ruleForm.workCity,
              catalogCode: this.ruleForm.positionCatalog,
              catalogFirst: CodeToTag(
                [
                  parseInt(
                    (parseInt(this.ruleForm.positionCatalog / 100) * 100) /
                      10000
                  ) * 10000,
                  parseInt(this.ruleForm.positionCatalog / 100) * 100,
                  this.ruleForm.positionCatalog
                ],
                this.positionCatalogList
              )[0],
              catalogSecondary: CodeToTag(
                [
                  parseInt(
                    (parseInt(this.ruleForm.positionCatalog / 100) * 100) /
                      10000
                  ) * 10000,
                  parseInt(this.ruleForm.positionCatalog / 100) * 100,
                  this.ruleForm.positionCatalog
                ],
                this.positionCatalogList
              )[1],
              catalogThird: CodeToTag(
                [
                  parseInt(
                    (parseInt(this.ruleForm.positionCatalog / 100) * 100) /
                      10000
                  ) * 10000,
                  parseInt(this.ruleForm.positionCatalog / 100) * 100,
                  this.ruleForm.positionCatalog
                ],
                this.positionCatalogList
              )[2],
              companyId: this.companyId,
              degreeMin: degreeName,
              degreeMinCode: this.ruleForm.degree,
              description: this.ruleForm.positionCatalogDetail,
              email: this.ruleForm.email,
              isGraduate: true,
              jobType: natureName,
              jobTypeCode: this.ruleForm.nature,
              managerId: this.ruleForm.HR,
              positionName: this.ruleForm.positionName,
              proxyEmail: this.ruleForm.email,
              requirement: this.ruleForm.JobSearch,
              salaryMax: salaryMax,
              salaryMin: salaryMin,
              published: true,
              sourceType: null,
              sourceUrl: null,
              workAgeMax: workAgeMax,
              workAgeMin: workAgeMin,
              offlineTime: logoutTime,
              publishedTime: publishedTime
            };
            this.$http
              .post("/business-core/positions", params)
              .then(res => {
                if (res.data.code == "200") {
                  this.$message({
                  message: res.data.message,
                  type: "success"
                });
                  this.$router.push({
                    path: "/position/info"
                  });
                } else {
                }
              })
              .catch(error => {
                console.log(error);
              });
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      } else {
        this.centerDialogVisiblesStatus = true
      }
    },
    //
    resetForm(formName) {
      this.ruleForm.desc = "";
      this.$refs[formName].resetFields();
    },
    removeDomain(item) {
      var index = this.ruleForm.domains.indexOf(item);
      if (index !== -1) {
        this.ruleForm.domains.splice(index, 1);
      }
    },
    addDomain() {
      this.ruleForm.domains.push({
        value: "",
        key: Date.now()
      });
    }
  },
  mounted: function() {},
  updated: function() {},
  created() {
    // this.companyID = this.$route.query.companyID;
    this.positionCatalogList = positionCatalog.data;
    this.cityList = city.data;
    this.ListHR();
    this.brief();
    this.state();
  }
};
</script>
<style>
.loading-texts {
  font-family: PingFangSC-Regular;
  color: #999999 100%;
  font-size: 18px;
  width: 80%;
  margin: 10px auto 30px;
}
.asp-content {
  width: 100%;
  background: #ffffff;
}
.asp-form {
  width: 98%;
  border: 1px solid #ffffff;
}
.asp-table {
  width: 100%;
  border: 1px solid #ffffff;
  background: #ffffff;
  margin: 10px 0 0 0;
}
</style>