  size: size,
  CodeToTag: CodeToTag,
  politicalStatus: politicalStatus,
  isUnified: isUnified,
  level: level,
  jobSearchStatus: jobSearchStatus,
  sub: sub,
  positionCatalog: positionCatalog,
  sex:sex,
